/*--------------------------------------------------------------
# Call to Action
--------------------------------------------------------------*/
.elementor .aarambha-kits-cta-wrapper {
  background-color: #27cfd8;
  padding: 64px 64px 64px 64px;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.elementor .aarambha-kits-cta-wrapper .icon-wrap .icon svg{
  fill: rgba(255,255,255,0.5);
  height: 120px;
  width: 120px;
}
.elementor .aarambha-kits-cta-wrapper .icon-wrap .icon i {
  color: rgba(255,255,255,0.5);
  font-size: 120px;
}
.elementor .flex-row.aarambha-kits-cta-wrapper .icon-wrap .image,
.elementor .flex-row.aarambha-kits-cta-wrapper .icon-wrap .icon {
  margin-right: 48px;
}
.elementor .flex-row-reverse.aarambha-kits-cta-wrapper .icon-wrap .image,
.elementor .flex-row-reverse.aarambha-kits-cta-wrapper .icon-wrap .icon {
  margin-left: 48px;
}
.elementor .flex-column.aarambha-kits-cta-wrapper .icon-wrap .image,
.elementor .flex-column.aarambha-kits-cta-wrapper .icon-wrap .icon {
  margin-bottom: 48px;
}
.elementor .flex-column-reverse.aarambha-kits-cta-wrapper .icon-wrap .image,
.elementor .flex-column-reverse.aarambha-kits-cta-wrapper .icon-wrap .icon {
  margin-top: 48px;
}
.elementor .aarambha-kits-cta-wrapper .details-wrap {
  max-width: 100%;
  width: 100%;
}
.elementor .aarambha-kits-cta-wrapper .details-wrap .entry-title {
  font-weight: 500;
  letter-spacing: 0.5px;
  color: #fff;
}

.elementor .aarambha-kits-cta-wrapper .details-wrap .entry-subtitle span {
  padding: 6px 12px;
  border-radius: 15px;
  color: #fff;
  background: mediumpurple;
  font-size: 11px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.elementor .aarambha-kits-cta-wrapper .details-wrap .entry-content p {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6em;
  color: #fff;
}
.elementor .aarambha-kits-cta-wrapper .details-wrap .button-wrap .button-link {
  padding: 8px 24px 8px 24px;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #fff;
  border-radius: 20px 20px 20px 20px;
  background-color: #1a88af;
}
.elementor .aarambha-kits-cta-wrapper .details-wrap .button-wrap .button-link:hover {
  color: #fff;
}
.post .aarambha-kits-cta-wrapper .entry-content {
  margin-bottom: 15px;
}

@media only screen and (max-width: 991px) {
.elementor .flex-row.aarambha-kits-cta-wrapper .icon-wrap .image, 
.elementor .flex-row.aarambha-kits-cta-wrapper .icon-wrap .icon {
  margin-right: 30px;
}

}

@media only screen and (max-width: 767px) {
.elementor .aarambha-kits-cta-wrapper {
  display: block;
  text-align: center;
}
.elementor .aarambha-kits-cta-wrapper .icon-wrap {
  display: block;
  margin-bottom: 20px;
}
.elementor .flex-row.aarambha-kits-cta-wrapper .icon-wrap .image, .elementor .flex-row.aarambha-kits-cta-wrapper .icon-wrap .icon {
  margin-right: 0;
}
.elementor .aarambha-kits-cta-wrapper .button-wrap {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.elementor .aarambha-kits-cta-wrapper {
  padding: 25px;
}

}

@media only screen and (max-width: 479px) {
.elementor .aarambha-kits-cta-wrapper {
  padding: 30px 15px;
}

}